


























import { mapState } from 'vuex';
import { Component, Vue } from 'vue-property-decorator';
import OrderItem from '@/components/OrderItem.vue';
import store from '@/store/store';
import { State, Order } from '@/store/state.interface';
import { bubble, sweetness } from '@/database/menu';

@Component({
  components: {
    OrderItem,
  },
  data() {
    return {
      username: this.$store.state.username,
    };
  },
  computed: {
    ...mapState({
      orders(state: State): Order[] {
        return state.orders.filter(o => o.owner === state.username);
      },
      loading(state: State) {
        return state.loading;
      },
      updating(state: State) {
        return state.updating;
      },
    }),
  },
  methods: {
    generateText(orders) {
      if (orders.length === 0) {
        this.$buefy.dialog.alert('ไม่มีรายการ');
      } else {
        const text = orders
          .map((each: Order, index: number) => {
            const bubbleLabel = bubble.find(b => b.value === each.bubble) || {
              label: '',
            };
            const sweetnessLabel = sweetness.find(s => s.value === each.sweetness) || {
              label: '',
            };
            const ownerLabel = each.owner ? `ของ ${each.owner}` : '';
            return `<li>
            ${index + 1}.
            ${each.name}
            ${bubbleLabel.label}
            ${sweetnessLabel.label}
            ${each.amount} แก้ว
            ${ownerLabel}
            </li>`;
          })
          .join('');
        this.$buefy.dialog.alert(`<ul>${text}</ul>`);
      }
    },
  },
})
export default class OrderList extends Vue {}
