























import { mapState } from 'vuex';
import { Component, Vue } from 'vue-property-decorator';
import store from '@/store/store';
import { State, Order } from '@/store/state.interface';
import { bubble, sweetness } from '@/database/menu';

@Component({
  data() {
    return {
      sweetnessList: sweetness,
      bubbleList: bubble,
      columns: [
        {
          field: 'name',
          label: 'ชื่อเมนู',
        },
        {
          field: 'bubble',
          label: 'ไข่มุก',
        },
        {
          field: 'sweetness',
          label: 'ระดับความหวาน',
        },
        {
          field: 'amount',
          label: 'จำนวนแก้ว',
        },
        {
          field: 'owner',
          label: 'คนสั่ง',
        },
      ],
      username: this.$store.state.username,
    };
  },
  computed: {
    ...mapState({
      orders(state: State): any[] {
        return state.orders.map(order => ({
          ...order,
          bubble: this.bubbleList.find((b: any) => b.value === order.bubble).label,
          sweetness: this.sweetnessList.find((s: any) => s.value === order.sweetness).label,
        }));
      },
      loading(state: State) {
        return state.loading;
      },
    }),
  },
  methods: {
    generateText(orders) {
      if (orders.length === 0) {
        this.$buefy.dialog.alert('ไม่มีรายการ');
      } else {
        const text = orders
          .map((each: Order, index: number) => {
            const bubbleLabel = bubble.find(b => b.value === each.bubble);
            const sweetnessLabel = sweetness.find(s => s.value === each.sweetness);
            const ownerLabel = each.owner ? `ของ ${each.owner}` : '';
            return `<li>
            ${'🔸 '}
            <span class="menu-name">${each.name}</span>
            ${bubbleLabel ? `<span class="bubble">${bubbleLabel.label}</span>` : ''}
            ${sweetnessLabel ? `<span class="sweetness">${sweetnessLabel.label}</span>` : ''}
            <span class="amount">${each.amount} แก้ว</span>
            <span class="owner">${ownerLabel}</span>
            </li>`;
          })
          .join('');
        this.$buefy.dialog.alert(`<ul>${text}</ul>`);
      }
    },
  },
})
export default class OrderList extends Vue {}
