
















































import { Component, Vue } from 'vue-property-decorator';
import { mapState } from 'vuex';
import store from '@/store/store';
import { Order, State } from '@/store/state.interface';
import {
  newMenu,
  milkTea,
  blackTea,
  thaiTea,
  soda,
  otherDrink,
  sweetness,
  bubble,
} from '@/database/menu';
import DrinkItem from '@/interfaces/DrinkItem';
import DrinkList from '@/components/DrinkList.vue';
import OrderList from '@/components/OrderList.vue';
import AddedOrderList from '@/components/AddedOrderList.vue';

@Component({
  components: {
    DrinkList,
    OrderList,
    AddedOrderList,
  },
  data() {
    return {
      newMenu,
      milkTea,
      blackTea,
      thaiTea,
      soda,
      otherDrink,
      orders: this.$store.state.orders,
      username: this.$store.state.username,
    };
  },
  computed: {
    ...mapState({
      cartItems(state: State): number {
        return state.orders.filter(o => o.owner === state.username).length;
      },
    }),
  },
  methods: {
    addOrder(drink: DrinkItem) {
      const order = {
        id: new Date().getTime(),
        name: drink.name,
        bubble: bubble[0].value,
        sweetness: sweetness[0].value,
        amount: 1,
        owner: this.$data.username,
      } as Order;
      store.dispatch('addOrder', order);
    },
    updateOrder(payload: Order) {
      store.dispatch('updateOrder', { ...payload, owner: this.$data.username });
    },
    removeOrder(e) {
      store.dispatch('removeOrder', e.id);
    },
  },
})
export default class MenuPage extends Vue {}
